const APP_ENV = process.env.VUE_APP__ENV;
let config = {};

if (APP_ENV === 'mock') {
  config = require('./config.mock.js');
} else if (APP_ENV === 'development') {
  config = require('./config.dev.js');
} else if (APP_ENV === 'production') {
  config = require('./config.prod.js');
}

module.exports = config;
